import { electronics } from "../../productArray";
import * as actiontypes from "./ChartTypes";

const initialState = {
  electronics: electronics,
  currentView: null,
  bag: electronics
}

 const chartReducer = (state = initialState, action) =>{

  switch(action.type){
    case actiontypes.ADD_TO_CART:
      return{
        ...state,
        bag: [...state.bag, action.payload]
      }
      break;
    case actiontypes.ADD_TO_CURRENT:
        return{
          ...state,
          currentView : action.payload
        }
     break;
    default :
    return state;
  }
}

export default chartReducer;