import React from 'react';

const Cart = ({ product }) => {

  return (
    <div className="cart">
            <div className="cart_box1">
              <div className="cart_img">
                <img src={product.img} alt={product.img}/>
              </div>
              <div className="cart_text">
                <p>{product.name}</p>
                <p>#{product.price}</p>
              </div>
            </div>
            <div className="btn_wrapper">
            <button>Remove</button>
            </div>
    </div>
  )
}

export default Cart;
